import React, { useState, useEffect } from 'react';
import marked from 'marked';
import { useHistory } from 'react-router-dom';
import { Row, Col, Input, Tag, Button, Popover, Avatar, Dropdown, Menu, Divider, message } from 'antd';
import {
    FileTextOutlined,
    EditOutlined,
    UserOutlined,
    CaretDownOutlined,
    CaretUpOutlined,
} from '@ant-design/icons';
import './AddArticle.scss'
import { ILoginInfo } from '../types';
import Request from '../../../utils/request';
import loginCon from 'container/loginCon';

const { TextArea } = Input
const { CheckableTag } = Tag;

const tagsFromServer = ['', '', ''];
const WriteArticle = (props: ILoginInfo) => {
    // const [articleId, setArticleId] = useState(0)  // ID00
    const [articleTitle, setArticleTitle] = useState('')   //
    const [articleContent, setArticleContent] = useState('')  //markdown
    const [markdownContent, setMarkdownContent] = useState('') //html
    const [introducemd, setIntroducemd] = useState()            //markdown
    const [introducehtml, setIntroducehtml] = useState('') //html
    const [userInfo, setUserInfo] = useState<ILoginInfo>();
    const [openMenu, setOpenMenu] = useState<boolean>(false);
    const [selectedTag, setSelectTag] = useState<number>();//
    const [showIntroduce, setShowIntroduce] = useState<boolean>(true);//
    const [introduceVal, setIndroduceVal] = useState<string>('')//

    marked.setOptions({
        renderer: new marked.Renderer(),
        gfm: true,
        pedantic: false,
        sanitize: false,
        breaks: false,
        smartLists: true,
        smartypants: false
    })

    const changeContent = (e: any) => {
        setArticleContent(e.target.value)
        let html = marked(e.target.value)
        setMarkdownContent(html)
    }
    const changeIntroduce = (e: any) => {
        setIntroducemd(e.target.value)
        setIndroduceVal(e.target.value)
        let html = marked(e.target.value)
        setIntroducehtml(html)
    }
    useEffect(() => {
        let info = localStorage.getItem('userInfo')
        if (info) {
            setUserInfo(JSON.parse(info))
        }
    }, [])

    const onGoTo = (url: string) => {
        console.log(url);

        history.push(url)
    }

    const addArticle = async () => {
        let req = new Request()
        await req.post('user/addArticle', {
            userId: userInfo?.userId,
            title: articleTitle,
            articleContent: articleContent,
            introduce: introduceVal,
            typeid: selectedTag
        }).then((res) => {
            message.success('~~~')
            setTimeout(() => onGoTo('/blog'), 1000)

        })
        // console.log(props.userId);

        // props.userId
    }

    const content = (
        <div className="choose-type">
            <p>:</p>
            <div>
                {tagsFromServer.map((tag, index) => <CheckableTag
                    key={tag}
                    checked={selectedTag === index + 1}
                    onChange={() => setSelectTag(index + 1)}
                >
                    {tag}
                </CheckableTag>)}
            </div>
            {/* <Divider className="push-divider" /> */}
            <Button size="small" className="push-button" onClick={addArticle}></Button>
        </div>
    );
    let history = useHistory();



    //
    const menu = (
        <Menu className="header_drow_menu">
            <Menu.Item key="0">
                <a onClick={() => onGoTo('/blog')} ><EditOutlined /> </a>
            </Menu.Item>
            <Menu.Item key="1">
                <a onClick={() => onGoTo(`/authorDetail/${userInfo?.userId}/articleList`)}><UserOutlined /> </a>
            </Menu.Item>
        </Menu>
    );


    return (
        <div>
            <Row gutter={5}>
                <Col span={24}>
                    <Row gutter={10} className="write-title">
                        <Col span={18}>
                            <Input
                                placeholder=""
                                size="large"
                                onChange={e => setArticleTitle(e.target.value)}
                            />
                        </Col>
                        <Col span={6} className="right-box">
                            <Button onClick={() => setShowIntroduce(!showIntroduce)}>{showIntroduce ? '' : ''} </Button>

                            <Popover content={content} title="" trigger="click">
                                <Button onClick={() => setOpenMenu(!openMenu)}>{openMenu ? <CaretUpOutlined /> : < CaretDownOutlined />} </Button>
                            </Popover>
                            <Dropdown overlay={menu} trigger={['click']}>
                                <a> <Avatar src={userInfo?.userIcon} /></a>
                            </Dropdown>,
                        </Col>
                    </Row>
                    {/* <br /> */}
                    {
                        showIntroduce && <Row gutter={10} className="input-introduce">
                            <Col span={12}>
                                <TextArea
                                    rows={4}
                                    className="input-introduce-textarea"
                                    placeholder=""
                                    defaultValue={introduceVal}
                                    onChange={changeIntroduce}
                                />
                            </Col>
                            <Col span={12}>
                                <div className="introduce-html"
                                    dangerouslySetInnerHTML={{ __html: introducehtml }}
                                >
                                </div>
                            </Col>
                        </Row>
                    }
                    <Row gutter={10}>
                        <Col span={12}>
                            <TextArea
                                className="markdown-content"
                                rows={35}
                                placeholder=""
                                onChange={changeContent}
                            />
                        </Col>
                        <Col span={12}>
                            <div className="show-html"
                                dangerouslySetInnerHTML={{ __html: markdownContent }}
                            ></div>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </div>
    )
}

export default WriteArticle